import React from 'react'
import { Formik, Form, Field, ErrorMessage } from 'formik'
import * as Yup from 'yup'
import { Checkbox, FormControlLabel } from '@mui/material'
import './RegistrationForm.scss'
import CustomTextField from './../UI/Input/CustomTextField'
import CustomButton from './../UI/Button/CustomButton'

const RegistrationForm = () => {
  const validationSchema = Yup.object().shape({
    firstName: Yup.string().required("  '"),
    lastName: Yup.string().required("  '"),
    company: Yup.string().required("  '"),
    phone: Yup.string()
      .required("  '")
      .matches(/^\d+$/, '     '),
    email: Yup.string().email(' email').required("  '"),
    question: Yup.string(),
    agreement: Yup.bool().oneOf([true], '    '),
  })

  return (
    <section className="section-form">
      <Formik
        initialValues={{
          firstName: '',
          lastName: '',
          company: '',
          phone: '',
          email: '',
          question: '',
          agreement: false,
        }}
        validationSchema={validationSchema}
        validateOnMount={true}
      >
        {({ isValid, dirty, errors, touched }) => (
          <Form className="form" id="registration-form">
            <div className="about-title">
              <p></p>
              <p>-</p>
            </div>

            <div className="input-group">
              <Field
                name="firstName"
                as={CustomTextField}
                label="' *"
                fullWidth
                error={touched.firstName && Boolean(errors.firstName)}
              />
              <ErrorMessage name="firstName" component="div" className="error-message" />
            </div>

            <div className="input-group">
              <Field
                name="lastName"
                as={CustomTextField}
                label=" *"
                fullWidth
                error={touched.lastName && Boolean(errors.lastName)}
              />
              <ErrorMessage name="lastName" component="div" className="error-message" />
            </div>

            <div className="input-group">
              <Field
                name="company"
                as={CustomTextField}
                label=" *"
                fullWidth
                error={touched.company && Boolean(errors.company)}
              />
              <ErrorMessage name="company" component="div" className="error-message" />
            </div>

            <div className="input-group">
              <Field
                name="phone"
                as={CustomTextField}
                label="   *"
                fullWidth
                error={touched.phone && Boolean(errors.phone)}
              />
              <ErrorMessage name="phone" component="div" className="error-message" />
            </div>

            <div className="input-group">
              <Field
                name="email"
                as={CustomTextField}
                label="   *"
                fullWidth
                error={touched.email && Boolean(errors.email)}
              />
              <ErrorMessage name="email" component="div" className="error-message" />
            </div>

            <div className="input-group">
              <Field
                name="question"
                as={CustomTextField}
                label={
                  <>
                     ,      
                    <br />
                          
                  </>
                }
                fullWidth
                multiline
                rows={2}
                error={touched.question && Boolean(errors.question)}
              />
            </div>

            <div className="input-group">
            <FormControlLabel
              control={
                <Field
                  name="agreement"
                  as={Checkbox}
                  sx={{
                    color: 'rgba(255, 255, 255, 0.05)',
                    '&.Mui-checked': {
                      color: 'rgba(255, 255, 255, 0.05)',
                    },
                    '&.Mui-checked .MuiSvgIcon-root': {
                      color: '#fff',
                    },
                  }}
                />
              }
              label={
                <span className="custom-label">
                  ,    {' '}
                  <span>  Uspacy</span>
                </span>
              }
            />
            <ErrorMessage name="agreement" component="div" className="error-message error-message-checkbox" />
            </div>

            <CustomButton
              type="submit"
              variant="submit"
              color="primary"
              disabled={!isValid || !dirty}
            >
              
            </CustomButton>
          </Form>
        )}
      </Formik>
    </section>
  )
}

export default RegistrationForm
